@import url(https://fonts.googleapis.com/css?family=Montserrat:500,700);
* {
  margin: 0;
  padding: 0;
  /* border: none; */
  box-sizing: border-box;
}

:root {
  --pastel-gray: #c9cebd;
  --malachite: #20bf55;
  --polished-pine: #6ba292;
  --tomato: #ff6542;
