  --black-coral: #59656f;
  --white-blue: #e1e8f0;
  --light-gray: #f5f4f4;
  --white: #ffffff;
}

html {
  font-size: 100%;
  font-weight: 500;
}

body {
  font-family: 'Montserrat', sans-serif;
  color: #59656f;
  color: var(--black-coral);
}

input:focus::-webkit-input-placeholder {
