}

.bg-light-gray {
  background-color: #f5f4f4;
  background-color: var(--light-gray);
}

.bg-green {
  background-color: #20bf55;
  background-color: var(--malachite);
}

.text-center {
  text-align: center;
}

.text-green {
  color: #20bf55;
  color: var(--malachite);
}
 
.text-red {
  color: #ff6542;
  color: var(--tomato);
}

